document.addEventListener('DOMContentLoaded',function(){
    iniccioapp();
})

function iniccioapp ()
{
    creagl();
    srcollnav();
    navfj();
}
function    creagl()
{
    const gl = document.querySelector('.gal-img');
   
    for(let i = 1; i <= 12; i++)
    {
        const img  = document.createElement('picture');
        img.innerHTML = `
      
        <source srcset="build/img/thumb/${i}.avif" type="image/avif">
        <source srcset="build/img/thumb/${i}.webp" type="image/webp">
        <img loading="lazy" width="200" height="300"  src="build/img/thumb/${i}.jpg" alt="imagen galeria  ">
     `; 
     img.onclick = function(){
        viewimg(i);
     }
     gl.appendChild(img)
    }
    function viewimg(id)
    {
        // console.log('carrgando...', id);
        const img  = document.createElement('picture');
        img.innerHTML = `
      
        <source srcset="build/img/grande/${id}.avif" type="image/avif">
        <source srcset="build/img/grande/${id}.webp" type="image/webp">
        <img loading="lazy" width="200" height="300"  src="build/img/grande/${id}.jpg" alt="imagen galeria  ">
     `;
     const ovrly = document.createElement('DIV');
     ovrly.appendChild(img);
     ovrly.classList.add('overlay');
     ovrly.onclick = function () {
        ovrly.remove();
        const bd = document.querySelector('body');
        bd.classList.remove('fijar-body');
     }

     const clsmd = document.createElement('P');
     clsmd.textContent = 'X';
     clsmd.classList.add('btn-cerrar')
        clsmd.onclick = function (){
            ovrly.remove();
            const bd = document.querySelector('body');
            bd.classList.remove('fijar-body');
        }
     ovrly.appendChild(clsmd);


     const bd = document.querySelector('body');
     bd.appendChild(ovrly);
     bd.classList.add('fijar-body')
    }

  
}
function srcollnav(){
    const enlc = document.querySelectorAll('.nav-prin a');
    enlc.forEach(enlc => {
        enlc.addEventListener('click', function(e){
            e.preventDefault();
            // console.log(e.target)
            const secscroll = e.target.attributes.href.value;
            const sec = document.querySelector(secscroll);
            sec.scrollIntoView({behavior : "smooth"});

        });
    } );
   }
   function navfj()
{
    const br = document.querySelector('.header');
    const sbfv = document.querySelector('.sb-fest');
    const bd  = document.querySelector('body')
    window.addEventListener('scroll', function(){
        // console.log(sbfv.getBoundingClientRect())
        if(sbfv.getBoundingClientRect().bottom < 0){
        //  console.log('fijo')
            br.classList.add('fijo');
            bd.classList.add('bd-scroll');
        }
        else{
           
            br.classList.remove('fijo');

            bd.classList.remove('bd-scroll');
        }
    });
}